.container {
  padding: 50px 30px;
  width: 100%;
  display: flex;
  gap: 30px;
  background-color: #c6d4ed;
  min-height: fit-content;
  align-items: center;
  box-shadow: 1px 1px 6px 2px rgba(0, 0, 0, 0.12);
  justify-content: space-evenly;
  color: #333;
}
.container .heading {
  margin: 0 auto;
  max-width: 100%;
  font-size: 2rem;
  font-weight: bold;
  letter-spacing: 1px;
  line-height: 3rem;
}

.list {
  padding: 20px;
  align-items: start;
}
.list .listItem {
  font-size: 1.2rem;
  line-height: 2rem;
}
@media (max-width: 768px) {
  .container {
    flex-direction: column;
    padding: 30px 15px;
  }

  .container .heading {
    font-size: 1rem;
    line-height: 1rem;
    max-width: 100%;
  }
}

@media (max-width: 576px) {
  .container .heading {
    font-size: 2rem;
    line-height: 3rem;
  }

  .right img {
    width: 200px;
  }
}
