.container {
  padding: 50px 30px;
  text-align: center;
  width: 100%;
  display: flex;
  gap: 30px;
  align-items: center;
  justify-content: space-evenly;
  background-color: #d9e3f6;
  min-height: 80vh;
}
.container .heading {
  margin: 0 auto;
  max-width: 500px;
  color: #000;
  font-size: 3rem;
  font-weight: bold;
  letter-spacing: 1px;
  line-height: 4.1rem;
}
.heading span {
  background-clip: text;
  background-image: linear-gradient(to right, #f284d3, #82adf6);
  color: transparent;
}

.right img {
  width: 400px;
}

@media (max-width: 768px) {
  .container {
    flex-direction: column;
    padding: 30px 15px;
    text-align: center;
  }

  .container .heading {
    font-size: 2.5rem;
    line-height: 3.5rem;
    max-width: 100%;
  }

  .right img {
    width: 300px;
  }
}

@media (max-width: 576px) {
  .container .heading {
    font-size: 2rem;
    line-height: 3rem;
  }

  .right img {
    width: 200px;
  }
}
