.container {
  display: flex;
  flex-direction: column;
  gap: 30px;
  min-width: 650px;
  max-width: 850px;
  margin: 0 auto;
  flex: 1.2;
  height: fit-content;
  min-height: 1120px;
  padding: 30px;
  box-shadow: 1px 1px 4px 2px rgba(0, 0, 0, 0.12);
}

.header {
  display: flex;
  flex-direction: column;
}
.header .heading {
  font-size: 2rem;
  font-weight: 500;
  text-transform: capitalize;
  margin-bottom: 5px;
}

.header .subheading {
  font-size: 1.2rem;
  color: var(--color);
  font-weight: 500;
}

.header .links {
  margin-top: 15px;
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
  row-gap: 10px;
}

.header .link {
  font-size: 0.875rem;
  display: flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
  text-decoration: none;
  color: black;
}
.header .link:visited {
  color: black;
}

.header .link svg {
  color: var(--color);
  height: 16px;
  width: 16px;
}

.main {
  display: flex;
  gap: 30px;
}

.col1 {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.col2 {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.section .sectionTitle {
  font-size: 1.4rem;
  font-weight: 500;
  width: 100%;
  color: var(--color);
  border-bottom: 2px solid var(--color);
}

.section .content {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px 0;
}

.section .content .item {
  border-bottom: 1px dotted lightgray;
  display: flex;
  flex-direction: column;
  gap: 5px;
  padding-bottom: 10px;
}

.section .content > .item:last-child {
  border-bottom: none;
}
.content .title {
  font-weight: 600;
  font-size: 0.875rem;
  line-height: 1.3rem;
}
.content .subTitle {
  color: rgb(107, 104, 104);
  font-size: 0.8rem;
}

.content .link {
  display: flex;
  gap: 5px;
  font-size: 0.75rem;
  cursor: pointer;
  color: var(--color);
  text-decoration: none;
}
.content .link:visited {
  color: var(--color);
}
.content .certificateLink {
  cursor: pointer;
  text-decoration: none;
  color: black;
}
.content .certificateLink:visited {
  color: black;
}
.content .link svg {
  color: var(--color);
  height: 14px;
  width: 14px;
}
.content .date {
  display: flex;
  gap: 5px;
  align-items: center;
  font-size: 0.75rem;
}
.content .overview {
  font-size: 0.75rem;
}
.content .date svg {
  color: var(--color);
  height: 14px;
  width: 14px;
}
.content .points {
  padding-left: 18px;
  font-size: 0.75rem;
}

.hidden {
  display: none;
}

.twoColumn {
  display: flex;
  flex: 1;
  flex-wrap: wrap;
  flex-direction: row !important;
  justify-content: space-between;
  gap: 20px !important;
}
.twoColumn > div {
  flex: 1;
  min-width: calc(50% - 10px);
  max-width: calc(50% - 10px);
}
.noborder {
  border-bottom: none !important;
  padding-bottom: 0px !important;
}

@media (max-width: 550px) {
  .container {
    min-width: auto;
    max-width: 100%;
    padding: 10px;
  }

  .section .content .item {
    flex-direction: column;
    gap: 5px;
    padding-bottom: 10px;
  }
}
