.container {
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.heading {
  font-weight: 500;
  font-size: 2rem;
}

.toolbar {
  width: 100%;
  display: flex;
  gap: 40px;
  justify-content: space-between;
  align-items: center;
}

.colors {
  display: flex;
  gap: 20px;
  padding: 0 30px;
}
.color,
.paintbrush {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  cursor: pointer;
  padding: 2px;
}
.colors .active {
  border: 2px solid #000;
}
.colorinput {
  padding-left: 10px;
}
.hidden {
  display: none;
  visibility: none;
}

.toolbar .button {
  display: flex;
  padding: 8px 16px;
  border-radius: 5px;
  background-color: #239ce2;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: 1px;
  gap: 5px;
  align-items: center;
  cursor: pointer;
}
.toolbar .button svg {
  width: 20px;
  height: 20px;
}
.button:hover {
  background-color: #60afdc;
}

.main {
  display: flex;
  flex-direction: column;
  gap: 30px;
  width: 100%;
}

@media (max-width: 768px) {
  .container {
    padding: 20px;
  }

  .heading {
    font-size: 1.5rem;
  }

  .toolbar {
    gap: 20px;
  }

  .colors {
    gap: 10px;
    padding: 0 10px;
  }
}

@media (max-width: 576px) {
  .container {
    padding: 10px;
  }

  .heading {
    font-size: 1.2rem;
  }

  .toolbar {
    gap: 10px;
    flex-direction: column;
    align-items: flex-start;
  }

  .colors {
    gap: 10px;
    padding: 0 10px;
  }

  .toolbar .button {
    font-size: 0.9rem;
  }
}
